<template>
  <div>
    <el-dialog
      title=""
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      :modal="false"
      width="80%"
    >
      <!-- <div class="search">
        <div style="margin-right: 0.2rem">
          <el-date-picker
            value-format="yyyy-MM-dd"
            v-model="date1"
            type="date"
            placeholder=""
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div style="margin-right: 0.2rem; color: #00b4fe">至</div>
        <div>
          <el-date-picker
            value-format="yyyy-MM-dd"
            v-model="date2"
            type="date"
            placeholder=""
            :clearable="false"
          >
          </el-date-picker>
          <button class="searchBth" @click="analyseHLXDetailFun()">查询</button>
        </div>
      </div> -->
      <div style="width: 100%; height: 6rem" ref="echart"></div>
    </el-dialog>
  </div>
</template>
<script>
import { analyseHLXDetail } from "@/network/inverter";
export default {
  data() {
    return {
      date1: "",
      date2: "",
      dialogVisible: false,
    };
  },
  watch: {
    // dialogVisible(val) {
    //   if (val) {
    //     setTimeout(() => {
    //       this.setEchart();
    //     }, 200);
    //   }
    // },
  },
  mounted() {},
  methods: {
    showDialogFun(item) {
      this.date2 = this.date1 =
        this.$getDate().year +
        "-" +
        this.$getDate().month +
        "-" +
        this.$getDate().day;
      // this.pvEqmId = id;
      this.dialogVisible = true;
      let contentObj = item;
      let keys = Object.keys(contentObj);
      let data = {
        name: contentObj["汇流箱名称"],
        children: [],
      };
      keys.forEach((item) => {
        let color = "red";
        if (contentObj[item] == 1) {
          color = "green";
        }
        if (item.includes("开关")) {
          data.children.push({
            name: item,
            value: contentObj[item],
            itemStyle: {
              color: color,
            },
          });
        }
      });
      setTimeout(()=>{
        this.setEchart(data);
      },1000)
      // this.analyseHLXDetailFun();
    },
    analyseHLXDetailFun() {
      analyseHLXDetail(this.pvEqmId, this.date1, this.date2)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          let contentObj = content[0];
          let keys = Object.keys(contentObj);
          let data = {
            name: contentObj["汇流箱名称"],
            children: [],
          };
          keys.forEach((item) => {
            let color = "red";
            if (contentObj[item] == 1) {
              color = "green";
            }
            data.children.push({
              name: item,
              value: contentObj[item],
              itemStyle: {
                color: color,
              },
            });
          });
          this.setEchart(data);
        })
        .catch((err) => {});
    },
    setEchart(data) {
      let chart = this.$refs.echart;
      let myChart = this.$echarts.init(chart);
      myChart.clear();
      let option = {
        tooltip: {
          trigger: "item",
          triggerOn: "mousemove",
        },
        series: [
          {
            type: "tree",
            data: [data],
            top: "1%",
            left: "7%",
            bottom: "1%",
            right: "20%",
            symbolSize: 7,
            symbol: "circle",
            label: {
              position: "left",
              verticalAlign: "middle",
              align: "right",
              fontSize: 9,
              textStyle: {
                color: "#fff",
              },
            },
            leaves: {
              label: {
                position: "right",
                verticalAlign: "middle",
                align: "left",
              },
            },
            emphasis: {
              focus: "descendant",
            },
            expandAndCollapse: true,
            animationDuration: 550,
            animationDurationUpdate: 750,
          },
        ],
      };
      myChart.setOption(option);
      window.addEventListener("resize", function () {
        myChart.resize();
      });
    },
  },
};
</script>
<style scoped lang="less">
/deep/ .el-dialog {
  background: #000000de;
}
/deep/ .el-dialog__headerbtn .el-dialog__close {
  color: #fff;
}
/deep/ .el-table tr {
  background: transparent;
  color: #fff;
}
/deep/ .el-table,
/deep/ .el-table__expanded-cell {
  background: transparent;
}
/deep/ .el-table th.el-table__cell {
  background: transparent;
}
/deep/
  .el-table--enable-row-hover
  .el-table__body
  tr:hover
  > td.el-table__cell {
  background-color: unset;
}
/deep/ .el-table td.el-table__cell,
/deep/ .el-table th.el-table__cell.is-leaf {
  border-bottom: 1px solid #5a5a5a;
}
/deep/ .el-table--border::after,
/deep/ .el-table--group::after,
/deep/ .el-table::before {
  background-color: unset;
}
.tabs {
  display: flex;
  p {
    background: rgba(0, 54, 129, 1);
    color: #fff;
    font-size: 0.17rem;
    padding: 0.08rem 0.35rem;
    margin-right: 0.06rem;
    border-radius: 0.05rem 0.05rem 0 0;
    cursor: pointer;
  }
}
.isActive {
  background: #0056dc !important;
}
.search {
  display: flex;
  align-items: center;
  > div {
    margin-right: 0.48rem;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
  }
}
//选择日期控件样式调整
/deep/ .el-input__prefix {
  right: 5px;
  left: unset;
}
/deep/ .el-input__inner {
  color: #fff;
}
/deep/ .el-input__icon {
  color: #00b4fe;
  font-size: 18px;
}
.searchBth {
  background: #0066ff;
  color: #fff;
  border-radius: 0.05rem;
  padding: 0 0.47rem;
  border: none;
  cursor: pointer;
  height: 40px;
  margin-left: 0.2rem;
}
</style>